
'use server';

type Route = {
    id: number;
    origin: string;
    destination: string;
    cargo: string;
    distance: number;
    game: 'ETS2' | 'ATS';
};

type ApiResponse = {
    status: boolean;
    response: Route[] | {};
};

export async function getRoutes(): Promise<Route[]> {
    try {
        const res = await fetch(`${process.env.NEXT_PUBLIC_APP_URL}/api/truckershub?endpoint=routes`);
        if (!res.ok) {
            console.error("Failed to fetch routes:", res.status, await res.text());
            return [];
        }

        const data: ApiResponse = await res.json();
        
        if (data && data.status && Array.isArray(data.response)) {
            return data.response;
        }

        return [];
    } catch (error) {
        console.error("Error fetching routes:", error);
        return [];
    }
}
